// Single-step highlight decoration (one at a time per editor)
let stepHighlightDecoration: vscode.TextEditorDecorationType | null = null;
function ensureStepHighlightDecoration() {
  if (!stepHighlightDecoration) {
    stepHighlightDecoration = vscode.window.createTextEditorDecorationType({
      isWholeLine: true,
      backgroundColor: 'rgba(255,215,0,0.25)',
      overviewRulerColor: 'rgba(255,215,0,0.8)',
      overviewRulerLane: vscode.OverviewRulerLane.Full,
      border: '1px solid rgba(255,215,0,0.7)'
    });
  }
  return stepHighlightDecoration;
}
function clearStepHighlight(editor?: vscode.TextEditor) {
  if (!stepHighlightDecoration) return;
  const ed = editor || vscode.window.activeTextEditor;
  if (ed && ed.document) {
    ed.setDecorations(stepHighlightDecoration, []);
  }
}
function applyStepHighlight(editor: vscode.TextEditor, line: number) {
  const deco = ensureStepHighlightDecoration();
  const lineIdx = Math.max(0, Math.min(editor.document.lineCount - 1, line - 1));
  const range = new vscode.Range(lineIdx, 0, lineIdx, editor.document.lineAt(lineIdx).text.length);
  editor.setDecorations(deco, [range]);
  try {
    if (vscode.window.activeTextEditor && vscode.window.activeTextEditor === editor) {
      editor.revealRange(range, vscode.TextEditorRevealType.InCenterIfOutsideViewport);
    }
  } catch { }
}

function showAndTrackOutputChannel(ch: vscode.OutputChannel) {
  ch.show(true);
  lastActiveOutputChannel = ch;
}

// Get or create an output channel for a document, used for per-sketch logging and errors
function getOrCreateOutputChannel(docUri: string, fileName: string) {
  let channel = outputChannelMap.get(docUri);
  if (!channel) {
    channel = vscode.window.createOutputChannel('LIVE P5: ' + fileName);
    outputChannelMap.set(docUri, channel);
  }
  lastActiveOutputChannel = channel; // <--- track whenever retrieved
  return channel;
}

// Debounce utility to delay function execution, used to avoid excessive reloads
function debounce<Func extends (...args: any[]) => void>(fn: Func, delay: number) {
  let timeout: NodeJS.Timeout;
  return (...args: Parameters<Func>) => {
    clearTimeout(timeout);
    timeout = setTimeout(() => fn(...args), delay);
  };
}

// Get current time as HH:MM:SS string for log timestamps
function getTime(): string {
  const now = new Date();
  const h = String(now.getHours()).padStart(2, '0');
  const m = String(now.getMinutes()).padStart(2, '0');
  const s = String(now.getSeconds()).padStart(2, '0');
  return `${h}:${m}:${s}`;
}

// Read debounce delay from user config
function getDebounceDelay() {
  return vscode.workspace.getConfiguration('P5Studio').get<number>('debounceDelay', 500);
}

// Recursively list .js/.ts files in a folder (for script imports)
async function listFilesRecursively(dirUri: vscode.Uri, exts: string[]): Promise<string[]> {
  let files: string[] = [];
  try {
    const entries = await vscode.workspace.fs.readDirectory(dirUri);
    for (const [name, type] of entries) {
      const entryUri = vscode.Uri.file(path.join(dirUri.fsPath, name));
      if (type === vscode.FileType.File && exts.some(ext => name.endsWith(ext))) {
        files.push(entryUri.fsPath);
      } else if (type === vscode.FileType.Directory) {
        files = files.concat(await listFilesRecursively(entryUri, exts));
      }
    }
  } catch (e) { }
  return files;
}

